@media print {
  html * {
    visibility: hidden; }
  #to-print, #to-print * {
    visibility: visible; }
  #to-print {
    font-size: 30px;
    position: absolute;
    left: 0;
    top: 0;
    padding: 20px; } }

@media (max-width: 768px) {
  nav div {
    visibility: hidden; }
  nav #menu {
    visibility: visible;
    display: inline-block; }
  .block {
    width: 100vw; }
  .block-title {
    display: flex;
    background: white;
    color: #6666cc;
    height: 40px;
    justify-content: center;
    align-items: center; }
  .day-time, .mbs {
    display: none; }
  .mobile-day-time {
    display: inline-block; }
  .block {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    border-left: none;
    border-right: none; }
  .club {
    width: 33%; }
  .dropdown {
    visibility: visible;
    position: absolute;
    z-index: 19;
    left: 0;
    top: -150px;
    display: flex;
    flex-direction: column;
    background: white; }
    .dropdown div {
      visibility: visible;
      text-align: center;
      border-top: #6666cc solid 1px;
      top: -158px;
      width: 100%;
      height: 25%; }
  .openDd {
    -webkit-animation: open 1s forwards;
            animation: open 1s forwards; }
  .closeDd {
    -webkit-animation: close 1s forwards;
            animation: close 1s forwards; }
  #closeClubs {
    display: none; } }

@media (orientation: landscape) {
	:root {
		--board-width: 95vh;
